        <div class="group">
          <div>
            <p>大奖池当前总量</p>
            <p>{{contractInfo.bigJackpot}} ETH</p>
          </div>
          <div>
            <p>小奖池当前总量</p>
            <p>{{contractInfo.smallJackpot}} ETH</p>
          </div>
          <div>
