<script>
    import mixins from '../mixins'
    import moment from 'moment';
    import Bn from 'bignumber.js'
    import * as api from '../api'
    import * as types from '../store/mutation-types'
    export default {
        name: "index",
        mixins: [mixins],
        data() {
            return {
                msg: [],
                bigTime: null,
                smallTime: null,
                today: moment().format('YYYY.MM.DD'),
                inPlanAmount: 0,
                rateAmount: 0,
                prize_list: [
                    {
                        icon: require("../assets/img/eth.png"), // 奖品图片
                        count: 0.01, // 奖品数量
                        isPrize: 1 // 该奖项是否为奖品
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.02,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.1,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 0.5,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 1,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 5,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 10,
                        isPrize: 1
                    },
                    {
                        icon: require("../assets/img/eth.png"),
                        count: 50,
                        isPrize: 1
                    }
                ], //奖品列表
                start_rotating_degree: 0, //初始旋转角度
                rotate_angle: 0, //将要旋转的角度
                start_rotating_degree_pointer: 0, //指针初始旋转角度
                rotate_angle_pointer: 0, //指针将要旋转的度数
                rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
                rotate_transition_pointer: "transform 12s ease-in-out", //初始化指针过度属性控制
                click_flag: true, //是否可以旋转抽奖
                index: 0
            }
        },
        filters:{
          flex(e,r){
              if(e){
                  return e.toFixed(r)
              }else{
                  return e;
              }
          },
          outNum(e){
              let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
              let c = [e.investGroup[0]*3,e.investGroup[1]*4,e.investGroup[2]];
              let s = new Bn(c[0]).plus(c[1]).plus(c[2]).toNumber();
              return s-n;
          },
          outPlan(e){
            let n = new Bn(e.aStatic3).plus(e.aStatic4).plus(e.aStatic5).toNumber();
            return n;
          },
          subString(e, r) {
              if(e){
                  return e.substring(0, 16) + '...' + e.substring(e.length - r)
              }else{
                  return  '';
              }
          }
        },
        computed: {
            url() {
                if (this.user) {
                    return `${this.sys.url}/?sup=${this.user.addr}`
                } else {
                    return `${this.sys.url}/`
                }
            },
            lottery_ticket(){
                let i=0;
                if(this.user&&this.user.sub){
                    i=this.user.sub.length;
                }
                if(this.user.invest>0) i++;
                return i-=this.user.prize;
            }
        },
        mounted() {
            // 初始化
            this.init_prize_list();
            this.countTime();
            let that = this;
            this.sockets.subscribe('reload', async data => {
                if(data.reload){
                    that.init();
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER,  user.user);
                }
            });

        },
        methods: {
            //此方法为处理奖品数据
            init_prize_list(list) {},
            async rotate_handle() {
                if(this.click_flag&&this.lottery_ticket>0){
                    let i = await api.getPrizeIndex();
                    this.index = i.data; //指定每次旋转到的奖品下标
                    this.rotating();
                }
            },
            rotating() {
                let that = this;
                let during_time = 5; // 默认为1s
                let random = Math.floor(Math.random() * 7);
                let result_index = this.index ; // 最终要旋转到哪一块，对应prize_list的下标
                let result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //最终会旋转到下标的位置所需要的度数
                let  rand_circle = 6; // 附加多转几圈，2-3
                this.click_flag = false; // 旋转结束前，不允许再次触发
                // 转动盘子
                let rotate_angle =
                    this.start_rotating_degree +
                    rand_circle * 360 +
                    result_angle[result_index] -
                    this.start_rotating_degree % 360;
                this.start_rotating_degree = rotate_angle;
                this.rotate_angle = "rotate(" + rotate_angle + "deg)";
                // 旋转结束后，允许再次触发
                setTimeout(async function() {
                    let user = await api.getUser();
                    that.$store.commit(types.UPDATE_USER, user.user);
                    that.click_flag = true;
                    that.game_over();
                }, during_time * 1000 + 1500); // 延时，保证转盘转完
            },
            game_over() {
                this.$Message.success("恭喜您，获得" +this.prize_list[this.index].count + " ETH");
            },
            // 初始化
            init() {
                let that = this;
                this.$store.dispatch('initService');
                this.countTime();
                setTimeout(function () {
                    that.$store.dispatch('newContract');
                }, 5000)
            },
            // 结算
            async settle(e,r){
                if(r>0){
                    this.$store.commit(types.LOADDING, true);
                    let a = await api.settle({type:e,amount:r});
                    if(a.success){
                        let user = await api.getUser();
                        this.$store.commit(types.UPDATE_USER,  user.user);
                        this.$store.commit(types.LOADDING, false);
                        this.$Message.success('结算成功！')
                    }
                }else{
                    this.$Message.error('结算失败！')
                }
            },
            async settleALL(){
                console.log(10)
                this.$store.commit(types.LOADDING, true);
                let r = new Bn(0).plus(this.user.static3).plus(this.user.static4).plus(this.user.static5).plus(this.user.dynamic)
                    .plus(this.user.shareholder).plus(this.user.angelAmount).toNumber();
                let a = await api.settle({type: 'all', amount: r});
                if (a.success) {
                    let user = await api.getUser();
                    this.$store.commit(types.UPDATE_USER, user.user);
                    this.$store.commit(types.LOADDING, false);
                    this.$Message.success('结算成功！')
                }
            },
            // 复制邀请码
            doCopy() {
                if(!this.user||!this.user._id) return this.$Message.error('您还不是本站会员！');
                if (this.account) this.$copyText(this.url).then(e => this.$Message.success('复制成功！'))
            },

            // 倒计时
            countTime: function () {
                let bTime = new Date(this.sys.bigTime).getTime() - new Date().getTime();
                let sTime = new Date(this.sys.smallTime).getTime() - new Date().getTime();
                if (bTime >= 0) this.bigTime = Math.floor(bTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(bTime / 1000 / 60 % 60) > 9 ? Math.floor(bTime / 1000 / 60 % 60) : '0' + Math.floor(bTime / 1000 / 60 % 60)) + ":" + (Math.floor(bTime / 1000 % 60) > 9 ? Math.floor(bTime / 1000 % 60) : '0' + Math.floor(bTime / 1000 % 60));
                if (sTime >= 0) this.smallTime = Math.floor(sTime / 1000 / 60 / 60 % 24) + ':' + (Math.floor(sTime / 1000 / 60 % 60) > 9 ? Math.floor(sTime / 1000 / 60 % 60) : '0' + Math.floor(sTime / 1000 / 60 % 60)) + ":" + (Math.floor(sTime / 1000 % 60) > 9 ? Math.floor(sTime / 1000 % 60) : '0' + Math.floor(sTime / 1000 % 60));
                setTimeout(this.countTime, 1000);
            },
            // 购买门票
            buyRate() {
                if(!this.user||!this.user._id) return ;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.rateAmount) || this.rateAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.tickets().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.rateAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        that.$store.commit(types.LOADDING, true);
                        if (hash) await api.saveHash({hash, type: 1, amount: that.rateAmount});
                        hashs = hash;
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 1, amount: that.rateAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        that.rateAmount = 0;
                        if (receipt.status) that.$Message.success('购买成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
            // 参与计划
            buyPlan() {
                if(!this.user||!this.user._id) return ;
                let that = this;
                if (!this.user) return that.$Message.error('您还不是本站会员！');
                if (isNaN(this.inPlanAmount) || this.inPlanAmount < 1 || this.rateAmount % 1 > 0) return this.$Message.error('输入错误！');
                let hashs = null;
                contract.methods.ticketConsume().send({
                    from: this.account,
                    value: web3.utils.toWei(new BN(this.inPlanAmount), 'ether')
                })
                    .on('transactionHash', async hash => {
                        if (hash) await api.saveHash({hash, type: 2, amount: that.inPlanAmount});
                        hashs = hash;
                        that.$store.commit(types.LOADDING, true);
                    })
                    .on('confirmation', function (confirmationNumber, receipt) {
                    })
                    .on('receipt', async receipt => {
                        await api.confirmHash({
                            item: {hash: hashs, type: 2, amount: that.inPlanAmount},
                            status: receipt.status
                        });
                        that.$store.commit(types.LOADDING, false);
                        if (receipt.status) that.$Message.success('参与成功！');
                    })
                    .on('error', error => {
                        that.$store.commit(types.LOADDING, false);
                    })
            },
        }
    }
</script>

