          .ivu-tabs-nav-scroll {
            .ivu-tabs-nav {
              .ivu-tabs-ink-bar {
                background-color: #2CC2FA;
              }

              .ivu-tabs-tab {
                color: #095D7E;
              }

              .ivu-tabs-tab-active {
                color: #29B8ED;
              }
            }
          }
        }

        .tabpane {
          padding: 0 1rem;

          p {
            color: #ffffff;
            font-size: 1rem;
            text-align: left;
          }

          .btn {
            width: 100%;
            background: #134B63;
            border-color: #134B63;
            color: #ffffff;
          }

        }
        .tabpane2 p{
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
        .tabpane1, .tabpane2, .tabpane3 {
          p {
            margin-bottom: 1rem;
          }

          .btn-group {
            display: flex;
            justify-content: space-between;
            margin: 0.5rem 0 1.5rem;

            button {
              border-color: #096183;
              background: #08121B;
              color: #096183;
            }

            button.active {
              color: #2CC2FA;
              border-color: #2CC2FA;
            }
          }

          .input {
            margin-bottom: 1.5rem;

            .ivu-input-group {
              top: 0;
            }

            input {
              border-color: #045976;
              background: #131F25;
              border-right: none;
              color: #2cc2fa;
            }

            input::-webkit-input-placeholder {
              color: #045976;
            }

            .ivu-input-group-append {
              border-color: #045976;
              background: #131F25;
              color: #2AB7EB;
            }
          }
        }

      }
    }

    .statistics {
      width: 100%;
      margin-top: 3rem;
      overflow: hidden;
      text-align: center;

      & > h1 {
        width: 150px;
        height: 45px;
        line-height: 45px;
        margin: 0 auto;
        background-image: url("../assets/OXO-DAPP_03.jpg");
        color: #2BBFF7;
        font-size: 1.5rem;
        text-align: center;
      }

      .group {
        width: 95%;
        margin: 1.5rem auto 0;

        div {
          display: flex;
          justify-content: space-between;
          height: 45px;
          line-height: 45px;
          border-bottom: 1px solid #031820;

          p {
            color: #096183;

            button {
              margin-left: .5rem;
              color: #ffffff;
              font-weight: bold;
              background: #134B63;
              border-color: #134B63;
            }
          }

          p:last-child {
            color: #2CC2FA;
          }
        }
      }

      .btn {
        width: 70%;
        margin: 1.5rem auto 0;
        background: #134B63;
        border-color: #134B63;
        color: #ffffff;
      }
    }

    .ranking {
      width: 100%;
      margin-top: 3rem;
      overflow: hidden;
      text-align: center;

      & > h1 {
        width: 150px;
        height: 45px;
        line-height: 45px;
        margin: 0 auto;
        background-image: url("../assets/OXO-DAPP_03.jpg");
        color: #2BBFF7;
        font-size: 1.5rem;
        text-align: center;
      }

      & > h2 {
        margin: 1.5rem 0;
        color: #f7f7f7;
      }

      .group {
        width: 95%;
        margin: 0 auto 1.5rem;

        div {
          display: flex;
          justify-content: space-between;
          height: 45px;
          line-height: 45px;
          border-bottom: 1px solid #031820;

          p {
            color: #096183;
          }

        }
      }
    }
  }

  .prize{
    margin-top: 3rem;
    & > h1 {
      width: 150px;
      height: 45px;
      line-height: 45px;
      margin: 0 auto 2rem;
      background-image: url("../assets/OXO-DAPP_03.jpg");
      color: #2BBFF7;
      font-size: 1.5rem;
      text-align: center;
    }
      .card{
        border: none;
        background: none;
        p{
          text-align: center;
          margin-top: 1rem;
          color: #096183;
        }
        .ivu-card-body{
          padding: 0;
          .lucky-wheel {
            width: 100%;
            height: auto;
            background: none;
            background-size: 100%;
            padding-bottom: 1rem;
            .wheel-main {
              display: flex;
              align-items: center;
              justify-content: center;
              position: relative;
              .wheel-pointer-box {
                position: absolute;
                top: 50%;
                left: 50%;
                z-index: 100;
                transform: translate(-50%, -60%);
                width: 3rem;
                height: 3rem;
                .wheel-pointer {
                  width: 3rem;
                  height: 3rem;
                  background: url("../assets/img/draw_btn.png") no-repeat center top /80%;
                  transform-origin: center 60%;
                }
              }
              .wheel-bg {
                width: 18.4375rem;
                height: 18.4375rem;
                background: url("../assets/img/draw_wheel.png") no-repeat center top;
                background-size: 100%;
                color: #fff;
                font-weight: 500;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-content: center;
                transition: transform 3s ease;
                .prize-list {
                  width: 100%;
                  height: 100%;
                  position: relative;
                  .prize-item {
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                    width: 5.875rem;
                    height: 9rem;
                    .prize-count {
                      margin-top: .3rem;
                      font-size: 1rem;
                    }
                    .prize-pic img {
                      width: 1.6rem;
                      margin-top: 3.2rem;
                      transform: rotate(70deg);
                    }
                    &:first-child {
                      top: 0;
                      left: 8.3125rem;
                      transform: rotate(20deg);
                    }
                    &:nth-child(2) {
                      top: 2.8rem;
                      left: 10.8rem;
                      transform: rotate(67deg);
                    }
                    &:nth-child(3) {
                      top: 6.4rem;
                      left: 10.6rem;
                      transform: rotate(-250deg);
                    }
                    &:nth-child(4) {
                      top: 9rem;
                      left: 8.2125rem;
                      transform: rotate(-210deg);
                    }
                    &:nth-child(5) {
                      top: 9.2125rem;
                      left: 4.4rem;
                      transform: rotate(-160deg);
                    }
                    &:nth-child(6) {
                      top: 6.3875rem;
                      left: 1.9rem;
                      transform: rotate(-111deg);
                    }
                    &:nth-child(7) {
                      top: 2.8rem;
                      left: 1.8125rem;
                      transform: rotate(-69deg);
                    }
                    &:nth-child(8) {
                      top: 0;
                      left: 4.5rem;
                      transform: rotate(-20deg);
                    }
                  }
                }
                div {
                  text-align: center;
                }
              }
            }
          }
        }
      }
  }
</style>



